.carousel-img-container {
    // height: 190px;
    padding:0;
    margin: 0;
    border-radius: 10px;
    // height: 100%;

    &::after{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-image: linear-gradient(transparent 75%, rgba(21, 21, 21, 0.3));
        border-radius: 10px;

    }

  overflow: hidden;
    .carousel-img{
        //  border-radius: 10px;
         height: 100%;
         
    }
    cursor: pointer;
}

.my-control {
    position: absolute;
    top: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 2;
    user-select: none;
    cursor: pointer;

    &-next {
        right: 12px;
        transition: all 0.35s;
    }

    &-prev {
        left: 12px;
        transition: all 0.35s;
    }
}

.my-arrow {
    background-color: rgba(255, 255, 255, 0.85);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    // border: 1px solid black;
    border-radius: 50%;
    transition: all 0.5s;

    &:hover {
       background-color: rgba(244, 244, 244, 0.85);
    }
}

.hide-to-right {
    right: -32px;
    transition: all 0.35s;
}

.hide-to-left {
    left: -32px;
    transition: all 0.35s;
}