.shopping-cart {
    display: flex;
    flex-direction: column;
    background-color: $clr6;
    z-index: 320;
    position: fixed;
    right: 0px;
    top: 0px;
    bottom: 0px;

    max-width: 480px;
    height: 100vh;

    color: $clr8;
    border-left: 1px solid #dadbdd;

    transform: translateX(100%);
    transition: transform 0.3s, box-shadow 0.6s;

    .cart-header {
        padding-inline-start: 32px;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #dadbdd;
        align-items: center;

        h2 {
            font-family: Macan-semibold;
            font-size: rem(16px);
            padding-block: 31px 23px;
        }

        svg {
            fill: $clr8;
            margin-inline-end: 32px;

            &:hover {
                cursor: pointer;
            }
        }
    }

    .purchase-details {
        padding-inline: 32px;
        display: flex;
        flex-direction: column;
        flex: 1;
        max-height: 100%;
        // align-content: center;
        // justify-content: space-between;

        .basic-information {
            border-bottom: 1px solid #dadbdd;
            // margin-block-end: 9em;

            h1 {
                font-family: Macan-semibold;
                font-size: rem(20px);
                line-height: 28px;
                margin-block: 2em;
            }

            p {
                font-size: rem(14px);
                padding-block-end: 1em;

                span {
                    font-family: Macan-semibold;
                }
            }
        }

        .total-order-wrapper {
            background-color: #f5f5f5;
            border-radius: 8px;
            color: #404145;
            padding: 32px 32px;
            flex: 1;

            p {
                font-size: rem(14px);

                span {
                    font-size: rem(28px);
                    font-family: Macan-semibold;
                }
            }

            .delivery-info-container {
                display: flex;
                flex-direction: column;
                align-content: center;
                font-size: rem(14px);
                font-family: Macan-semibold;
                color: #62646a;
                margin-block: 1.8em 0.2em;

                div {
                    display: flex;
                    align-items: center;

                    svg {
                        fill: $clr5;
                        margin-inline-end: 1em;
                    }

                    .delivery-days-item {
                        margin-inline-end: 1em;
                    }
                }
            }

        }

        .pricing-footer {
            border-top: 1px solid #dadbdd;
            // margin-block-start: 5em;

            .btn-continue-purchase {
                background-color: $clr7;
                font-size: rem(16px);
                margin: 0;
                margin-block-start: 2em;

                padding: 12px 24px;
                border: 1px solid transparent;
                border-radius: 4px;
                min-width: 100%;
                position: relative;
            }

            .not-charging {
                text-align: center;
                font-size: rem(12px);
                color: $clr5;
                margin-block-end: 1em;
            }
        }
    }
}