.gig-details-main {

    &.isOpen {
        .shopping-cart {
            transform: translate(0%, 0%);
        }

        .main-screen {
            pointer-events: auto;
            opacity: 1;
        }
    }

    .top-nav-container {
        position: sticky;
        left: 0;
        top: 0;
        width: 100%;
        z-index: 50;
        backface-visibility: hidden;
        border-bottom: 1px solid $grey2;
        background-color: #ffff;

        nav {
            display: flex;
            align-items: center;
            padding-bottom: 0;
            padding-top: 0;
            box-sizing: border-box;
            position: relative;

            //mq for screen min-width 1160px
            margin: 0 auto;
            max-width: 1400px;

            a {
                font-size: rem(14px);
                padding-inline-end: 1em;
                text-decoration: none;
                color: #95979d;
                display: inline-block;
                font-weight: 600;
                line-height: 53px;
                transition: color .2s;

                &.selected {
                    color: #222325;
                }
            }
        }
    }


    .gig-details-content {
        display: grid;

        @include for-normal-layout {
            grid-template-columns: 66.4% 33.6%;
            padding: 3% 5%;
        }

        .gig-details-container {       
            margin-inline-end: 1%;

            @include for-normal-layout {
                margin-inline-end: 9.15%;
            }

            h1 {
                color: $clr8;
                font-size: rem(28px);
                font-family: Macan-bold;
                line-height: 130%;
                padding-block-start: 0em;
                margin-block: -0.2em 0.67em;
                margin-inline-end: 2em;
                letter-spacing: 0.2px;
                overflow-wrap: break-word;
            }

            .main-details-container {
                display: flex;
                align-items: center;
                flex-direction: row;
                justify-content: flex-start;
                margin-block-end: 24px;
                font-family: Macan;

                .user-round-img {
                    margin-right: 1em;

                    .details-owner-img {
                        border-radius: 50%;
                        height: 30px;
                        width: 30px;
                    }
                }

                .details-wrapper {
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;

                    h2 {
                        font-family: Macan-bold;
                        color: $clr8;
                        cursor: pointer;
                        margin-inline-end: 0.5em;
                        // padding-inline: 0em 0.625em;
                        font-size: rem(18px);
                        line-height: 1.44;
                        letter-spacing: 0.01px;
                    }

                    p {
                        margin-inline-end: 0.5em;
                        font-size: rem(14px);
                    }

                    .gig-email,
                    .gig-orders {
                        color: $clr4;
                        letter-spacing: 0.4px;
                        padding-inline-end: 0.5em;
                    }

                    .gig-level {
                        color: $clr5;
                        // padding-inline-end: 8px;
                        span {
                            color: $grey;
                            padding-inline-start: 8px;
                        }
                    }

                    .gig-starRate {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        color: $clr5;
                        padding-inline-end: 8px;
                        color: $clr4;

                        .owner-rate {
                            color: #ffb33e;
                            font-family: Macan-semibold;
                            font-size: rem(14px);

                            span {
                                color: $clr4;
                                font-family: Macan-light;
                                font-size: rem(14px);
                                padding-left: 4px;
                            }
                        }
                    }
                }
            }

            .carousel-wrapper {
                max-width: 90vw;
            }
        }

        .pricing-container {
            max-width: 100%;

            @include for-normal-layout {
                grid-area: 1/2/5/-1
            }
        }

        .about-gig-container {
            color: $clr8;
            margin-inline-end: 1%;
            margin-inline-end: 0;

            @include for-normal-layout {
                margin-inline-end: 9.15%;
            }

            h3 {
                font-family: Macan-semibold;
                font-size: rem(20px);
                line-height: 28px;
                padding: 25px 0px 25px;
            }

            p {
                color: $clr5;
                font-size: rem(16px);
                white-space: pre-line;
                font-family: Macan;
                line-break: auto;
                margin-block-end: 1em;
            }
        }

        .about-seller-container,
        .gig-review{
            margin-inline-end: 1%;

            @include for-normal-layout {
                margin-inline-end: 9.15%;
            }
        }
    }
}